*,
*:before,
*:after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.currency-container{
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
}
.currency-intake{
  position: relative;
}
.currency h1{
  text-align: center;
}
.currency-input{
  display: flex;
}
.currency-input input{
  width: 100%;
  padding: 5px 10px;
  font-size: 16px;
}
.currency-input button{
  font-size: 16px;
  padding: 0 10px;
}
.currency-list{
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #fff;
  border: 1px solid #000;
  max-height: 300px;
  overflow-y: auto;
  width: 100%;
}
.currency-list ul{
  padding-left: 0;
  margin: 0;
  list-style: none;
}
.currency-list ul li{
  padding: 5px;
}
.currency-list ul li:hover{
  background-color: rgba(0,0,0,0.3);
  color: #fff;
  cursor: pointer;
}
.currency-table{
  border-collapse: collapse;
}
.currency-table caption{
  border: 1px solid #000;
  border-bottom: none;
  padding: 5px 20px;
}
.currency-table td{
  border: 1px solid #000;
  padding: 2px 20px;
}



